// Links
.px2-a {
	&::before {
		content: ' ';
		width: 0;
		height: 0;
		display: inline-block;
		margin-right: 0.2em;
		border-width: 0.35em 0 0.35em 0.6em;
		border-style: solid;
		border-color: var(--px2-main-color, #00a0e6);
		border-top-color: transparent;
		border-bottom-color: transparent;
	}
}
.px2-open-in-new-window {
	&::after {
		content: '';
		width: 0.8em;
		height: 0.8em;
		display: inline-block;
		background-size: contain;
		background-repeat: no-repeat;
		margin-left: 0.3em;
	}
}

// Buttons
.px2-btn {
	display: inline-block;
	border-radius: 4px;
	background-color: #fff;
	color: #000;
	border: 1px solid #e5e5e5;
    padding: .375rem .75rem;
	font-size: 1em;
	font-weight: normal;
	line-height: 1.5;
	text-decoration: none;
	text-align: center;
	cursor: pointer;
	box-sizing: border-box;
	align-items: stretch;
	transition:
		color 0.1s,
		background-color 0.1s
	;

	&:focus,
	&:hover {
		text-decoration: none;
		font-weight: normal;
		color: #333;
		border-color: #333;
		background-color: #f0f0f0;
	}
	&:focus {
		color: #333;
		border-color: #333;
		background-color: #f0f0f0;
	}
	&:hover {
		border-color: #333;
		background-color: #f0f0f0;
	}
	&:active {
		background-color: #e6e6e6;
		color: #333;
		box-shadow: inset 0 3px 15px rgba(0,0,0,0.3);
	}
}

// Intention
.px2-btn {

	&.px2-btn--primary {
		border-color: #00a0e6;
		background-color: #00a0e6;
		color: #fff;
		&:focus,
		&:hover {
			border-color: #3ac4ff;
			background-color: #3ac4ff;
		}
		&:hover {
			border-color: #3ac4ff;
			background-color: #3ac4ff;
		}
		&:active {
			border-color: #0c6e98;
			background-color: #0c6e98;
		}
	}
	&.px2-btn--secondary {
		border-color: #00a0e6;
		background-color: #fff;
		color: #00a0e6;
		&:focus,
		&:hover {
			border-color: #3ac4ff;
			background-color: #eefaff;
			color: #3ac4ff;
		}
		&:hover {
			border-color: #3ac4ff;
			background-color: #eefaff;
			color: #3ac4ff;
		}
		&:active {
			border-color: #3ac4ff;
			background-color: #eefaff;
			color: #3ac4ff;
		}
	}
	&.px2-btn--danger {
		border-color: #e86a60;
		background-color: #e86a60;
		color: #fff;
		&:focus,
		&:hover {
			border-color: #ff928a;
			background-color: #ff928a;
		}
		&:hover {
			border-color: #ff928a;
			background-color: #ff928a;
		}
		&:active {
			border-color: #d71f12;
			background-color: #d71f12;
		}
	}
	&.px2-btn--download {
		border-color: #4ad64d;
		background-color: #4ad64d;
		color: #fff;
		&:focus,
		&:hover {
			border-color: #75ef77;
			background-color: #75ef77;
		}
		&:hover {
			border-color: #75ef77;
			background-color: #75ef77;
		}
		&:active {
			border-color: #24b827;
			background-color: #24b827;
		}
	}

	&[disabled] {
		border-color: #ddd;
		background-color: #ddd;
		color: #fff;
		cursor: default;

		&:focus,
		&:hover,
		&:active {
			border-color: #ddd;
			background-color: #ddd;
			color: #fff;
			cursor: default;
			box-shadow: none;
		}
	}
}


// Toggle ON
.px2-btn {
	&--toggle-on {
		box-shadow: inset 0 3px 15px rgba(0, 0, 0, 0.25);
		border-color: #000;
		background-color: #000;
		color: #fff;

		&:focus,
		&:hover {
			box-shadow: inset 0 3px 15px rgba(0, 0, 0, 0.25);
			border-color: #666;
			background-color: #666;
			color: #fff;
		}
		&:active {
			box-shadow: inset 0 3px 15px rgba(0, 0, 0, 0.25);
			border-color: #111;
			background-color: #111;
			color: #ddd;
		}

		&[disabled] {
			border-color: #ddd;
			background-color: #ddd;
			color: #fff;
			cursor: default;
			box-shadow: inset 0 3px 15px rgba(0, 0, 0, 0.25);

			&:focus,
			&:hover,
			&:active {
				border-color: #ddd;
				background-color: #ddd;
				color: #fff;
				cursor: default;
				box-shadow: inset 0 3px 15px rgba(0, 0, 0, 0.25);
			}
		}

		&.px2-btn--secondary {
			&:active {
				color: #00a0e6;
			}
			&[disabled] {
				&:active {
					color: #fff;
				}
			}
		}
	}
}

// Sizing
.px2-btn {

	&--sm {
		font-size:0.8em;
	}
	&--lg {
		font-size:1.2em;
	}
	&--block {
		display: block;
		width: 100%;
	}

}


// Radio Switch
.px2-radio-switch {
	position: relative;
	margin-top: 1em;
	margin-bottom: 1em;
	padding: 10px 0;
	overflow: auto;
	background-color: #f9f9f9;
	border-radius: 13px;
	white-space: nowrap;
	box-shadow: inset 0 3px 15px rgba(0, 0, 0, 0.05);
	text-align: center;
	box-sizing: border-box;

	> ul {
		padding: 0 15px;
		margin: 0;
		display: inline-flex;
		gap: 5px;
		justify-content: flex-start;
		overflow: auto;
		max-width: 100%;
		box-sizing: border-box;
		flex-wrap: nowrap;
		> li {
			list-style-type: none;
			padding: 0;
			margin: 0;
		}
	}
	input[type=radio] {
		position: absolute;
		top: 0;
		left: 0;
		visibility: hidden;
	}
	label {
		display: block;
	}
	&__btn {
		display: inline-block;
		border-radius: 4px;
		background-color: #fff;
		color: #000;
		border: 1px solid #e5e5e5;
		padding: .375rem .75rem;
		font-size: 1em;
		font-weight: normal;
		line-height: 1.5;
		text-decoration: none;
		text-align: center;
		cursor: pointer;
		box-sizing: border-box;
		align-items: stretch;
		transition:
			color 0.1s,
			background-color 0.1s
		;

		&:focus,
		&:hover {
			text-decoration: none;
			font-weight: normal;
			color: #333;
			border-color: #333;
			background-color: #f0f0f0;
		}
		&:focus {
			color: #333;
			border-color: #333;
			background-color: #f0f0f0;
		}
		&:hover {
			border-color: #333;
			background-color: #f0f0f0;
		}
		&:active {
			background-color: #e6e6e6;
			color: #333;
			box-shadow: inset 0 3px 15px rgba(0,0,0,0.3);
		}
	}
	input[type=radio]:checked + &__btn {
		box-shadow: inset 0 3px 15px rgba(0, 0, 0, 0.25);
		border-color: #000;
		background-color: #000;
		color: #fff;

		&:focus,
		&:hover {
			box-shadow: inset 0 3px 15px rgba(0, 0, 0, 0.25);
			border-color: #666;
			background-color: #666;
			color: #fff;
		}
		&:active {
			box-shadow: inset 0 3px 15px rgba(0, 0, 0, 0.25);
			border-color: #111;
			background-color: #111;
			color: #ddd;
		}
	}
}









// --------------------------------------
// darkmode

.px2-btn{
	background-color: $darkmode-background-color;
	color: $darkmode-text-color;
	border: 3px solid $darkmode-text-color;
	box-shadow: 0 2px 0px rgba(255, 255, 255, 0.1);

	&:focus,
	&:hover{
		color: #eee;
		background-color: #222;
	}
	&:focus{
		box-shadow: 0 0 0 .2rem rgba(123,230,255,.25);
	}
	&:hover{
		background-color: #292929;
	}
	&:active{
		background-color: $darkmode-text-color;
		color: $darkmode-background-color;
		box-shadow: inset 0 3px 15px rgba(255, 255, 255, 0.3);
	}
}

// Elements
.px2-btn{

	&.px2-btn--primary{
		border-color: #00a0e6;
		background-color: #d9f1fb;
		color: #00a0e6;
		&:focus,
		&:hover{
			background-color: #ccecfa;
		}
		&:hover{
			background-color: #f5fbfe;
		}
		&:active{
			background-color: #00a0e6;
			color: #fff;
		}
	}
	&.px2-btn--secondary{
		border-color: $darkmode-text-color;
		background: $darkmode-background-color;
		color: $darkmode-text-color;
		&:focus,
		&:hover{
			color: #eee;
			background-color: #222;
		}
		&:hover{
			background-color: #292929;
		}
		&:active{
			background-color: $darkmode-text-color;
			color: $darkmode-background-color;
		}
	}
	&.px2-btn--danger{
		border-color: #e86a60;
		background-color: #fdf8f8; // rgba(232,106,96,0.05);
		color: #e86a60;
		&:focus,
		&:hover{
			background-color: #fce9e7; // rgba(232,106,96,0.15);
		}
		&:hover{
			background-color: #fae1df; // rgba(232,106,96,0.3);
		}
		&:active{
			background-color: #e86a60;
			color: #fff;
		}
	}
	&.px2-btn--download{
		border-color: #4ad64d;
		background-color: #f7fcf7; // rgba(74,214,77,0.05);
		color: #4ad64d;
		&:focus,
		&:hover{
			background-color: #e4f9e4; // rgba(74,214,77,0.15);
		}
		&:hover{
			background-color: #dbf7db; // rgba(74,214,77,0.3);
		}
		&:active{
			background-color: #4ad64d;
			color: #fff;
		}
	}

	&[disabled]{
		border-color: #444;
		background-color: rgba(153,153,153,0.05);
		color: #444;
		&:focus,
		&:hover,
		&:active{
			border-color: #444;
			background-color: rgba(153,153,153,0.05);
			color: #444;
			box-shadow: 0 2px 0px rgba(255, 255, 255, 0.1);
