	}
}


// Toggle ON
.px2-btn{
	&--toggle-on{
		box-shadow: inset 0 3px 15px rgba(255, 255, 255,0.15);

		&[disabled]{
			box-shadow: inset 0 3px 15px rgba(255, 255, 255,0.15);
			&:focus,
			&:hover,
			&:active{
				box-shadow: inset 0 3px 15px rgba(255, 255, 255,0.15);
			}
